<template>
  <div>
    <div class="rekapitulasi-daftar">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pt-4 pb-6" type="primary"></base-header>
      <div v-if="renders.loading" class="container-fluid mt-4">
        <loading
          :active.sync="renders.loading"
          :can-cancel="true"
          :on-cancel="onCancel"
          :is-full-page="true"
          loader="dots"
        ></loading>
      </div>
      <div v-if="!renders.loading" class="container-fluid mt--6">
        <div class="card">
          <div
            class="border-0 card-header d-flex align-items-center justify-content-between"
          >
            <h3 class="mb-0">Rekapitulasi Rating</h3>
            <div>
              <el-select
                class="my--1 mr-2"
                v-if="isAdmin"
                v-model="selects.unitID"
                placeholder="Unit Kerja"
                @change="getData(selects.unitID, modal.periode)"
              >
                <el-option
                  v-for="option in cabang"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <el-select
                class="my--1 mr-2"
                size="mini"
                v-model="modal.periode"
                placeholder="Pilih Periode"
                @change="getData(selects.unitID, modal.periode)"
              >
                <el-option
                  label="SEMUA PERIODE"
                  value="SEMUA PERIODE"
                ></el-option>
                <el-option
                  v-for="option in periode"
                  :key="option.value"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <base-button
                v-if="!isAdmin"
                size="md"
                type="primary"
                @click="tambahRating"
                >Tambah</base-button
              >
            </div>
          </div>

          <el-table
            class="table-responsive table-flush"
            header-row-class-name="thead-light"
            :data="queriedData"
            :default-sort="{ prop: 'timestamp', order: 'descending' }"
            @sort-change="sortChange"
            @selection-change="selectionChange"
            @row-click="openData"
            highlight-current-row
          >
            <el-table-column
              label="Tanggal"
              prop="timestamp"
              min-width="176px"
              align="center"
              sortable
            >
              <template v-slot="{ row }">
                <span>{{ unixTS2DMY(row.timestamp) }}</span>
              </template>
            </el-table-column>
            <el-table-column
              label="Cabang/Unit Bisnis"
              min-width="264px"
              prop="unitName"
            ></el-table-column>
            <el-table-column label="Periode" prop="periode" min-width="264px">
            </el-table-column>
            <el-table-column
              label="Hasil Rating"
              min-width="176px"
              prop="rating"
              align="right"
            >
              <template v-slot="{ row }">
                <span>{{ ratingHandler(row).toFixed(2) }}</span>
              </template>
            </el-table-column>
            <el-table-column label="Level" min-width="176px">
              <template v-slot="{ row }">
                <span>{{ levelHandler(ratingHandler(row)) }}</span>
              </template>
            </el-table-column>
          </el-table>
          <div
            slot="footer"
            class="col-12 px-4 py-2 d-flex justify-content-center justify-content-sm-between flex-wrap"
          >
            <div class="d-flex align-items-center">
              <p class="card-category mb-0 mr-2">Menampilkan</p>
              <div style="width: 80px">
                <el-select
                  class="select-primary pagination-select"
                  v-model="pagination.perPage"
                  placeholder="Per page"
                  size="mini"
                >
                  <el-option
                    class="select-primary"
                    v-for="item in pagination.perPageOptions"
                    :key="item"
                    :label="item"
                    :value="item"
                  ></el-option>
                </el-select>
              </div>
              <p class="card-category m-0 ml-2">
                Tampil {{ from + 1 }} sampai {{ to }} dari {{ total }} data
                <span v-if="selectedRows.length"
                  >&nbsp; &nbsp; {{ selectedRows.length }} rows selected</span
                >
              </p>
            </div>
            <base-pagination
              class="pagination-no-border my-3"
              v-model="pagination.currentPage"
              :per-page="pagination.perPage"
              :total="total"
            ></base-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination } from "@/components";
import moment from "moment";
import clientPaginationMixin from "../../Tables/PaginatedTables/clientPaginationMixinMista";
import cabang from "../cabang";
import rating from "./rating";
import { keys } from "d3";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

const axios = require("axios");

const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};

export default {
  mixins: [clientPaginationMixin],
  components: {
    Loading,
    BasePagination,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      renders: {
        loading: true,
      },
      api: this.$store.state.api,
      cabang,
      count: {
        kp: 23,
        pg: 16,
        pl: 14,
        mk: 12,
        ku: 26,
        kl: 11,
        at: 6,
        dp: 17,
        dk: 4,
        em: 17,
        ai: 12,
        hs: 8,
        ml: 17,
        lb: 32,
        sb: 6,
      },
      max: {
        kp: 15,
        pg: 10,
        pl: 1.5,
        mk: 1.5,
        ku: 6,
        kl: 6,
        at: 6,
        dp: 6,
        dk: 5,
        em: 25,
        ai: 7.5,
        hs: 1.5,
        ml: 1.5,
        lb: 6,
        sb: 1.5,
      },
      portion: {
        kp: [0.2, 0.2, 0.1, 0.1, 0.2, 0.2],
        pg: [0.3, 0.3, 0.4],
        pl: [0.15, 0.15, 0.125, 0.125, 0.2, 0.125, 0.125],
        mk: [0.3, 0.4, 0.3],
        ku: [0.1, 0.1, 0.05, 0.05, 0.2, 0.25, 0.05, 0.2],
        kl: [0.3, 0.3, 0.3, 0.1],
        at: [0.15, 0.2, 0.15, 0.2, 0.15, 0.15],
        dp: [0.1, 0.1, 0.1, 0.05, 0.05, 0.1, 0.1, 0.4],
        dk: [0.65, 0.35],
        em: [0.2, 0.6, 0.2],
        ai: [0.1, 0.1, 0.3, 0.5],
        hs: [0.15, 0.3, 0.2, 0.2, 0.15],
        ml: [0.2, 0.1, 0.1, 0.2, 0.4],
        lb: [0.2, 0.075, 0.1, 0.075, 0.1, 0.075, 0.1, 0.075, 0.2],
        sb: [0.4, 0.3, 0.3],
      },
      start: {
        kp: [0, 5, 8, 12, 15, 20],
        pg: [0, 5, 10],
        pl: [0, 1, 2, 5, 6, 7, 9],
        mk: [0, 3, 8],
        ku: [0, 4, 7, 9, 12, 16, 19, 20],
        kl: [0, 2, 5, 7],
        at: [0, 1, 2, 3, 4, 5],
        dp: [0, 1, 2, 3, 4, 5, 6, 7],
        dk: [0, 2],
        em: [0, 6, 14],
        ai: [0, 3, 5, 10],
        hs: [0, 1, 3, 5, 7],
        ml: [0, 3, 4, 5, 6],
        lb: [0, 11, 13, 16, 20, 21, 23, 25, 27],
        sb: [0, 2, 4],
      },
      span: {
        kp: [5, 3, 4, 3, 5, 3],
        pg: [5, 5, 6],
        pl: [1, 1, 3, 1, 1, 2, 5],
        mk: [3, 5, 4],
        ku: [4, 3, 2, 3, 4, 3, 1, 6],
        kl: [2, 3, 2, 4],
        at: [1, 1, 1, 1, 1, 1],
        dp: [1, 1, 1, 1, 1, 1, 1, 10],
        dk: [2, 2],
        em: [6, 8, 3],
        ai: [3, 2, 5, 2],
        hs: [1, 2, 2, 2, 1],
        ml: [3, 1, 1, 1, 11],
        lb: [11, 2, 3, 4, 1, 2, 2, 2, 5],
        sb: [2, 2, 2],
      },
      rating,
      ratings: [],
      rekapitulasi: 0,
      selects: {
        unitID: this.$store.getters.getUKId,
      },
      selectedRows: [],
      tableData: [],
      periode: [],
      modal: {
        periode: "SEMUA PERIODE",
      },
    };
  },
  computed: {
    isAdmin() {
      return this.$store.getters.isAdmin;
    },
  },
  methods: {
    periodeRNG() {
      return this.periode[intRNG(0, this.periode.length - 1)].PERIODE;
    },
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then((response) => {
          let array = [];
          for (let index = 0; index < response.data.length; index++) {
            array.push({
              label: response.data[index].PERIODE,
              value: response.data[index].PERIODE,
            });
          }
          array.sort((a, b) => {
            return -1;
          });
          this.periode = array;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    tambahRating() {
      this.$router.push("tambah");
    },
    openData(row, col, event) {
      // console.log(row);
      this.$store.commit("setGPData", row);
      this.$router.push("rekapitulasi");
    },
    getData(unit, periode) {
      if (this.$store.getters.isDebugging) {
        console.log("DEBUG: Getting rating list");
      }
      let url = null;
      if (periode === "SEMUA PERIODE") {
        url = this.api.url + this.api.gp.getAllRating + unit;
      } else {
        url =
          this.api.url +
          this.api.gp.getAllRating +
          unit +
          "?periode=" +
          periode;
      }
      console.log(url);
      axios
        .get(url)
        .then((response) => {
          if (this.$store.getters.isDebugging) {
            console.log(
              "DEBUG: Success to get rating list. Data: ",
              response.data
            );
          }
          let array = [];
          for (let i = 0; i < response.data.length; i++) {
            array.push(this.passDATA(response.data[i]));
          }
          array.sort((a, b) => {
            let aVal = a.timestamp;
            let bVal = b.timestamp;
            return bVal - aVal;
          });
          this.tableData = array;
          this.renders.loading = false;
        })
        .catch((error) => {
          if (this.$store.getters.isDebugging) {
            console.log("ERROR: Failed to get rating list. ", error);
            this.$router.push("tambah");
          }
        });
    },
    passDATA(data) {
      var container = {};
      container.id = data.GP_ID;
      container.timestamp = data.CREATEDAT / 1000;
      container.unitName = data.UK_NAME;
      container.periode = data.GP_PERIODE;
      container.kp = data.KP;
      container.pg = data.PG;
      container.pl = data.PL;
      container.mk = data.MK;
      container.ku = data.KU;
      container.kl = data.KL;
      container.at = data.AT;
      container.dp = data.DP;
      container.dk = data.DK;
      container.em = data.EM;
      container.ai = data.AI;
      container.hs = data.HS;
      container.ml = data.ML;
      container.lb = data.LB;
      container.sb = data.SB;
      return container;
    },
    ratingHandler(data) {
      let aspect = {
        kp: 0,
        pg: 0,
        pl: 0,
        mk: 0,
        ku: 0,
        kl: 0,
        at: 0,
        dp: 0,
        dk: 0,
        em: 0,
        ai: 0,
        hs: 0,
        ml: 0,
        lb: 0,
        sb: 0,
      };
      Object.keys(aspect).forEach((key) => {
        aspect[key] = this.aspectHandler(key, data[key]);
      });

      return this.recaptHandler(aspect);
    },
    aspectHandler(aspect, data) {
      let value = 0;

      for (let i = 0; i < this.portion[aspect].length; i++) {
        value = this.percentageHandler(aspect, data);
      }
      return value;
    },
    percentageHandler(aspect, data) {
      let percentage = [];

      for (let i = 0; i < this.span[aspect].length; i++) {
        let sum = 0;
        for (
          let j = this.start[aspect][i];
          j < this.start[aspect][i] + this.span[aspect][i];
          j++
        ) {
          sum += Math.abs(data[j]);
        }
        percentage.push(
          (sum / (this.span[aspect][i] * 2)) * this.portion[aspect][i] * 100
        );
      }

      let total = 0;
      for (let i = 0; i < percentage.length; i++) {
        total += percentage[i];
      }

      return total;
    },
    recaptHandler(aspect) {
      Object.keys(aspect).forEach((key) => {
        aspect[key] *= this.max[key] / 100;
      });

      let recapt = 0;
      Object.values(aspect).forEach((value) => {
        recapt += value;
      });
      return recapt;
    },
    unixTS2DMY(timestamp) {
      return moment.unix(timestamp).format("DD/MM/YYYY");
    },
    levelHandler(rating) {
      if (rating >= 91) return "Excellent";
      else if (rating >= 81) return "Good";
      else if (rating >= 66) return "Medium";
      else if (rating >= 51) return "Poor";
      else return "Very Poor";
    },
    selectionChange(selectedRows) {
      this.selectedRows = selectedRows;
    },
    onCancel() {
      this.$router.push("tambah");
    },
  },
  created() {
    this.$store.commit("setGPData", null);
    this.getListPeriode();
    if (this.isAdmin) {
      this.getData(9999, "SEMUA PERIODE");
    } else {
      this.getData(this.$store.getters.getUKId, "SEMUA PERIODE");
    }
  },
};
</script>

<style lang="scss">
.rekapitulasi-daftar {
  .el-table {
    th.is-right {
      .cell {
        display: flex;
        flex-flow: row;
        justify-content: flex-end;
      }
    }
    th.is-center {
      .cell {
        display: flex;
        flex-flow: row;
        justify-content: center;
      }
    }
  }
}
</style>
